// import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux'
import { addItem } from 'redux/basketSlice'
import {
  AccentContainer,
  InfoContainer,
  BtnContainer,
  Item,
  Link,
  List,
  ListText,
  Text,
  TextContainer,
  Title,
  TitleContainer, Span} from './Info.styled'
const Info = ({ data }) => {
  const dispatch = useDispatch()

  
  const { title, description, size, price, } = data
  
    return (
        <InfoContainer>
          <TitleContainer>
            <Title>{title}</Title>
          </TitleContainer>
          <AccentContainer>
            <Text>     </Text>
          </AccentContainer>
          <TextContainer>
            <List>
              <Item>
                <ListText><Span>:</Span>{description}</ListText>
              </Item>
              <Item>
                <ListText><Span>:</Span>{size}</ListText>
              </Item>
              <Item>
                <ListText><Span>:</Span>{price}</ListText>
              </Item>
            </List>
          </TextContainer>
          <BtnContainer>
            <Link to="basket" onClick={()=>{dispatch(addItem(title, price))}}> </Link>
          </BtnContainer>
        </InfoContainer>
    )
}



export default Info